import React from "react";
import styles from "./AdminNavbar.module.css";
import { NavLink, Redirect } from "react-router-dom";
import Bytepad from '../../../assets/images/SI.svg';

export default function AdminNavbar() {
  const [redirect, setRedirect] = React.useState(null);

  // When user isn't logged in
  let loggedIn = [{ name: "Login", link: "login" }];

  // When user is logged in
  if (localStorage.getItem("token") != null) {
    loggedIn = [
      { name: "AddPapers", link: "AddPapers" },
      { name: "ViewPapers", link: "ViewPapers" },
      { name: "AddFields", link: "Edit" },
      { name: "Delete", link: "Delete" },
      { name: "Logout" },
    ];
  }

  // logout function
  const logout = () => {
    localStorage.clear();
    setRedirect("/siadmin/login");
  };

  // To redirect to login page when logout is clicked
  if (redirect != null) {
    return <Redirect to={redirect} />;
  }

  return (
    <div className={styles.Navbar}>
      <nav>
        <ul>
          <img className={styles.logo} src={Bytepad} alt="bytepad"/>
          {loggedIn.map((item, index) => {
            return (
              <React.Fragment key={index}>
                {item.name === "Logout" ? (
                  <li className={styles.logout} onClick={logout}>
                    {item.name}
                  </li>
                ) : (
                  <NavLink
                    activeStyle={{ borderBottom: "white 1.4px solid" }}
                    to={`/siadmin/${item.link}`}
                  >
                    <li>{item.name}</li>
                  </NavLink>
                )}
              </React.Fragment>
            );
          })}
        </ul>
      </nav>
    </div>
  );
}
