import React, { useState } from "react";
import { Tabs } from "antd";
import { Collapse } from "antd";
import { DeleteTwoTone } from "@ant-design/icons";
import styles from "./ViewPapers.module.css";
import { message } from "antd";
import "./AdminView.css";
import ServerService from "../../../API/ServerService";
import Url from "../../../API/baseURL/Url";

const { TabPane } = Tabs;
const { Panel } = Collapse;

const SessionsTabsDisplay = (props) => {
  // Initialisation of state
  const [sessionsData, setSessionsData] = useState(props.sessionsData);
  const [courseData] = useState(props.courseData);
  const [branchData] = useState(props.branchData);

  //Checking if onClick works on Tab and Panel
  const sessionHandler = (key) => {
    // console.log(key);
  };

  const ExamTypeHandler = (key) => {
    // console.log(key);
  };

  const PaperTypeHandler = (key) => {
    // console.log(key);
  };

  const previewDoc = (data) => {  

    console.log(data);

    const previewPath = `https://docs.google.com/viewer?url=${Url}api/user/preview/${data.documentId}`;
    
    let docURI = data.documentURI;
    let split = docURI.split(".");
    let docType = split[split.length - 1];

    if(docType === 'pdf'){
      // alert('PDF__');
      window.open(`${Url}api/user/preview/${data.documentId}`)
      return;
    }
    window.open(previewPath);
  };

  // Function to delete a single paper
  const deletePaper = (
    e,
    docId,
    paperIndex,
    paperTypeIndex,
    examTypeIndex,
    sessionIndex
  ) => {
    e.preventDefault();
    // console.log(docId, paperIndex, paperTypeIndex, examTypeIndex, sessionIndex);

    let deletePaperData = {
      courseId: "",
      branchId: "",
      sessionId: "",
      examId: "",
      paperType: "",
      documentId: docId,
      password: "",
    };
    // console.log(deletePaperData);

    if (
      window.confirm(
        "Are you sure you want to delete this document from database?"
      )
    ) {
      //loading msg
      let key = "updatable";
      message.loading({ content: "Action in progress...", key });

      // Calling the Delete Api
      ServerService.DeleteTypes(deletePaperData)
        .then((res) => {
          // console.log(res);
          if (res.status === 200) {
            let newSessionsData = [...sessionsData];
            newSessionsData[sessionIndex].exams[examTypeIndex].paperTypes[
              paperTypeIndex
            ].documents.splice(paperIndex, 1);
            setSessionsData(newSessionsData);
            message.success({
              content: "Document deleted!",
              key,
              duration: 2,
            });
          }
        })
        .catch((err) => {
          // console.log(err);
          message.error({ content: "Something went wrong!", key, duration: 2 });
        });
    }
  };

  // Function to delete the Paper Type (eg. QUES OR ANS)
  const deletePaperType = (
    e,
    examId,
    paperType,
    paperTypeIndex,
    examTypeIndex,
    sessionIndex
  ) => {
    e.preventDefault();
    const password = prompt("Please enter your password");
    // console.log(examId, paperTypeIndex, examTypeIndex, sessionIndex);
    // console.log(sessionsData)

    let deletePaperData = {
      courseId: courseData.courseId,
      branchId: branchData.branchId,
      sessionId: sessionsData[sessionIndex].sessionId,
      examId: examId.examId,
      paperType: paperType.paperType,
      documentId: "",
      password: password,
    };
    // console.log(deletePaperData);

    if (password !== null) {
      //loading msg
      let key = "updatable";
      message.loading({ content: "Action in progress...", key });

      // Calling the API
      ServerService.DeleteTypes(deletePaperData)
        .then((res) => {
          // console.log(res);
          if (res.status === 200) {
            let newSessionsData = [...sessionsData];
            newSessionsData[sessionIndex].exams[
              examTypeIndex
            ].paperTypes.splice(paperTypeIndex, 1);
            setSessionsData(newSessionsData);
            message.success({
              content: "Paper Type deleted!",
              key,
              duration: 2,
            });
          }
        })
        .catch((err) => {
          // console.log(err);
          message.error({ content: "Something went wrong!", key, duration: 2 });
        });
    }
  };

  // Function to delete The exam type (eg: PUT, ST ...)

  const deleteExamType = (e, examId, examTypeIndex, sessionIndex) => {
    e.preventDefault();
    const password = prompt("Please enter your password");
    // console.log(examId, examTypeIndex, sessionIndex);

    let deletePaperData = {
      courseId: courseData.courseId,
      branchId: branchData.branchId,
      sessionId: sessionsData[sessionIndex].sessionId,
      examId: examId,
      paperType: "",
      documentId: "",
      password: password,
    };
    // console.log(deletePaperData);

    if (password !== null) {
      //loading msg
      let key = "updatable";
      message.loading({ content: "Action in progress...", key });

      // Calling the API
      ServerService.DeleteTypes(deletePaperData)
        .then((res) => {
          // console.log(res);
          if (res.status === 200) {
            let newSessionsData = [...sessionsData];
            newSessionsData[sessionIndex].exams.splice(examTypeIndex, 1);
            setSessionsData(newSessionsData);
            message.success({
              content: "ExamType-data deleted!",
              key,
              duration: 2,
            });
          }
        })
        .catch((err) => {
          // console.log(err);
          message.error({ content: "Something went wrong!", key, duration: 2 });
        });
    }
  };

  // Function to delete a particular session

  const deleteSession = (e, sessionIndex) => {
    e.preventDefault();
    const password = prompt("Please enter your password");
    // console.log(sessionIndex);

    let deletePaperData = {
      courseId: courseData.courseId,
      branchId: branchData.branchId,
      sessionId: sessionsData[sessionIndex].sessionId,
      examId: "",
      paperType: "",
      documentId: "",
      password: password,
    };
    // console.log(deletePaperData);

    if (password !== null) {
      //loading msg
      let key = "updatable";
      message.loading({ content: "Action in progress...", key });

      // Calling the API
      ServerService.DeleteTypes(deletePaperData)
        .then((res) => {
          // console.log(res);
          if (res.status === 200) {
            let newSessionsData = [...sessionsData];
            newSessionsData.splice(sessionIndex, 1);
            setSessionsData(newSessionsData);
            message.success({
              content: "Session-data deleted!",
              key,
              duration: 2,
            });
          }
        })
        .catch((err) => {
          // console.log(err);
          message.error({ content: "Something went wrong!", key, duration: 2 });
        });
    }
  };

  return (
    <Tabs
      defaultActiveKey="0"
      tabPosition={"left"}
      // style={{ height: 220 }}
      onChange={sessionHandler}
    >
      {sessionsData.map((session, sessionIndex) => {
        let ExamTypes = session.exams;
        return (
          <TabPane
            tab={
              <div style={{ display: "flex", alignItems: "center" }}>
                <span>{session.session}</span>{" "}
                <DeleteTwoTone
                  style={{
                    transform: "scale(1.3)",
                    cursor: "pointer",
                    marginLeft: "14px",
                  }}
                  onClick={(e) => deleteSession(e, sessionIndex)}
                  twoToneColor="#eb2f96"
                />
              </div>
            }
            key={sessionIndex}
          >
            <Tabs
              defaultActiveKey="0"
              tabPosition={"top"}
              // style={{ height: 220 }}
              onChange={ExamTypeHandler}
            >
              {ExamTypes.map((ExamTypes, examTypeIndex) => {
                return (
                  <TabPane
                    tab={
                      <div style={{ display: "flex", alignItems: "center" }}>
                        <span>{ExamTypes.exam}</span>{" "}
                        <DeleteTwoTone
                          style={{
                            transform: "scale(1.3)",
                            cursor: "pointer",
                            marginLeft: "14px",
                          }}
                          onClick={(e) =>
                            deleteExamType(
                              e,
                              ExamTypes.examId,
                              examTypeIndex,
                              sessionIndex
                            )
                          }
                          twoToneColor="#eb2f96"
                        />
                      </div>
                    }
                    key={examTypeIndex}
                  >
                    <Collapse onChange={PaperTypeHandler}>
                      {ExamTypes.paperTypes.map((examType, paperTypeIndex) => {
                        return (
                          <Panel
                            header={
                              <>
                                <span>{examType.paperType}</span>&nbsp;
                                <span
                                  style={{ color: "grey" }}
                                >{`(${examType.documents.length} docs)`}</span>
                                <DeleteTwoTone
                                  style={{
                                    transform: "scale(1.3)",
                                    cursor: "pointer",
                                    marginLeft: "14px",
                                  }}
                                  onClick={(e) =>
                                    deletePaperType(
                                      e,
                                      ExamTypes,
                                      examType,
                                      paperTypeIndex,
                                      examTypeIndex,
                                      sessionIndex
                                    )
                                  }
                                  twoToneColor="#eb2f96"
                                />
                              </>
                            }
                            key={paperTypeIndex}
                          >
                            <div className={styles.diplaySection}>
                              {examType.documents.map((doc, docIndex) => {
                                return (
                                  <p className={styles.papers} key={docIndex}>
                                    <span
                                      style={{ cursor: "pointer" }}
                                      onClick={() => previewDoc(doc)}
                                    >
                                      {doc.subjectName}
                                    </span>
                                    <DeleteTwoTone
                                      style={{
                                        transform: "scale(1.3)",
                                        cursor: "pointer",
                                        marginLeft: "14px",
                                      }}
                                      onClick={(e) =>
                                        deletePaper(
                                          e,
                                          doc.documentId,
                                          docIndex,
                                          paperTypeIndex,
                                          examTypeIndex,
                                          sessionIndex
                                        )
                                      }
                                      twoToneColor="#eb2f96"
                                    />
                                  </p>
                                );
                              })}
                            </div>
                          </Panel>
                        );
                      })}
                    </Collapse>
                  </TabPane>
                );
              })}
            </Tabs>
          </TabPane>
        );
      })}
    </Tabs>
  );
};

export default SessionsTabsDisplay;
