import React from "react";
import AdminNavbar from "../../../Components/Admin/Navbar/Navbar";
import "./Edit.css";
import { message } from "antd";
import ServerService from "../../../API/ServerService";
import { Tag } from "antd";

export default function EditFields() {

  // State initilisation
  const [data, setData] = React.useState(null);
  const [course, setCourse] = React.useState("");
  const [branch, setBranch] = React.useState([]);
  const [session, setSession] = React.useState([]);
  const [ExamType, setExamType] = React.useState([]);

  // Sumbit function
  const SumbitHandler = () => {
    const form = {
      courseBranch: [
        {
          branches: branch,
          course: course ? course : null,
        },
      ],
      examTypes: ExamType,
      sessions: session,
    };

    // means if the branch is selected
    if (branch.length !== 0 && course === "") {
      let key = "updatable";
      message.error({
        content: "Can't send branch without course!",
        key,
        duration: 2,
      });
    } 
    else {
      if (!course) {
        form["courseBranch"] = [];
      }

      const password = prompt("Enter your password");
      form["password"] = password;

      console.log(form);
      if (password != null) {
        // Calling the API
        //loading msg
        let key = "updatable";
        message.loading({ content: "Action in progress...", key });
        ServerService.editFields(form)
          .then(() => {
            // console.log(res);
            message.success({
              content: "Fields added successfully!",
              key,
              duration: 2,
            });
          })
          .catch(() => {
            // console.log(err);
            message.error({
              content: "Something went wrong!",
              key,
              duration: 2,
            });
          });
      }
    }
  };

  // Calling API to fetch data
  React.useEffect(() => {
    ServerService.getUploadData()
      .then((res) => {
        // console.log(res);
        setData(res.data);
      })
      .catch((err) => {
        // console.log(err);
      });
  }, []);

  return (
    <div>
      <AdminNavbar />
      <div className="Edit">
        <div className="Edit_fields">
          <div className="Edit_fields_name">
            <p>Course</p>
            <input
              onChange={(e) => setCourse(e.target.value)}
              placeholder="Enter course"
              className="Edit_fields_input"
              value={course}
            />
          </div>
          <p className="examples">
            <b>Existing Courses:&nbsp; </b>{" "}
            {data
              ? data.courseBranch.map((course, index) => {
                  return (
                    <Tag
                      onClick={() => setCourse(course.course)}
                      key={index}
                      color="processing"
                    >
                      {course.course}
                    </Tag>
                  );
                })
              : null}
          </p>
          <div className="Edit_fields_name">
            <p>Branch</p>
            <input
              onChange={(e) => {
                setBranch([e.target.value]);
              }}
              placeholder="(make sure a course is selected)"
              className="Edit_fields_input"
            />
          </div>
          <p className="examples">
            <b>For eg:</b> <Tag color="success">CSE</Tag>
          </p>
          <div className="Edit_fields_name">
            <p>Session</p>
            <input
              onChange={(e) => {
                setSession([e.target.value]);
              }}
              placeholder="Enter session"
              className="Edit_fields_input"
            />
          </div>
          <p className="examples">
            <b>For eg:</b> <Tag color="success">2020-2021</Tag>
          </p>
          <div className="Edit_fields_name">
            <p>Exam Type</p>
            <input
              onChange={(e) => {
                setExamType([e.target.value]);
              }}
              placeholder="Enter Exam type"
              className="Edit_fields_input"
            />
          </div>
          <p className="examples">
            <b>For eg:</b> <Tag color="success">PUT</Tag>
          </p>
        </div>
      </div>

      <div className="Sumbit_button">
        <button onClick={SumbitHandler}>Add Fields</button>
      </div>
    </div>
  );
}
