import React, { useCallback } from "react";
import { useDropzone } from "react-dropzone";
import Uploadcsv from "../../assets/images/Upload_csv.svg";
import classes from "./DropZone.module.css";

function DropZone(props) {
  const onDrop = useCallback((acceptedFiles) => {
    // alert('FILESS');
    let filesList = [];
    acceptedFiles.forEach((file) => {
      filesList.push(file);
    });
    // console.log(filesList);
    props.updateFiles(filesList);
  });

  const { acceptedFiles, getRootProps, getInputProps } = useDropzone({
    onDrop,
  });

  return (
    <section className={classes.container}>
      <div {...getRootProps({ className: classes.dropzone })}>
        <input multiple={false} {...getInputProps()} />
        <img
          src={Uploadcsv}
          style={{ width: "23px", height: "24px" }}
          alt="uploader"
        ></img>
        <p style={{ fontSize: "10px" }}>
          <h6>
            {acceptedFiles.length > 0
              ? `${acceptedFiles.length} Files Uploaded!`
              : "Upload your files"}
          </h6>
        </p>
        <p style={{ fontSize: "10px" }}>
          <h6>Drag 'n' drop some files here, or click to select files</h6>
        </p>
      </div>
    </section>
  );
}
export default DropZone;
