import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { HashRouter } from "react-router-dom";
import ContentPage from "./routes/ContentPage/ContentPage";
import LandingPage from "./routes/LandingPage/LandingPage";
import Login from "./routes/Admin/Login/Login";
import AddPapers from "./routes/Admin/AddPapers/AddPapers";
import ViewPapers from "./routes/Admin/ViewPapers/ViewPapers";
import EditFields from "./routes/Admin/Edit/Edit";
import DeleteField from "./routes/Admin/DeleteField/DeleteField";
import ResetPassword from "./routes/Admin/ResetPassword/ResetPassword";

function App() {

  const AdminGuardRoute = ({ component: Component, ...props }) => (
    <Route
      {...props}
      render={(routeProps) => {
        const item = localStorage.getItem("token");

        // check to see if the user is authenticated or not
        return item !== null ? (
          <Component {...routeProps} />
        ) : (
          <Redirect to="/" />
        );
      }}
    />
  );

  return (
    <HashRouter>
      <Switch>

        {/* User routes */}
        <Route path="/" exact component={LandingPage} />
        <Route path="/content" component={ContentPage} />
  
        {/* Admin routes   */}
        <AdminGuardRoute path="/siadmin/AddPapers" exact component={AddPapers} />
        <AdminGuardRoute path="/siadmin/ViewPapers" exact component={ViewPapers} />
        <AdminGuardRoute path="/siadmin/Edit" exact component={EditFields} />
        <AdminGuardRoute path="/siadmin/Delete" exact component={DeleteField} />

        <Route path="/siadmin/login" exact component={Login} />
        <Route
          path="/api/admin/resetPassword/:token"
          exact
          component={ResetPassword}
        />

        <Redirect to="/" />
      </Switch>
    </HashRouter>
  );
}

export default App;
