import React from "react";
import AdminNavbar from "../../../Components/Admin/Navbar/Navbar";
import styles from "./Delete.module.css";
import { message } from "antd";
import { Button } from "antd";
import ServerService from "../../../API/ServerService";

export default function DeleteField() {

  // data stores the fetched data from backend
  const [data, setData] = React.useState(null);
  // session and examType hold the selected values
  const [session, setSession] = React.useState(null);
  const [examType, setExamType] = React.useState(null);

  // store the values in the state
  const saveHandler = (e, option) => {
    // option is either sesison or exam
    if (option === "session") {
      setSession([e.target.value]);
    }
    if (option === "exam") {
      setExamType([e.target.value]);
    }
  };

  // Sumbit function
  const DeleteHandler = (option) => {
    // option is either sesison or exam
    // session is an object ["id:id,index:index"]

    let key = "updatable";
    const form = {};

    if (option === "session") {
      if (session == null) {
        message.error({ content: "Session not selected!", key, duration: 2 });
      } else {
        // using split to seperate the sessionId and index
        const sessionData = session[0].split(",");

        form["id"] = sessionData[0];
        const password = prompt("Enter Password");
        form["password"] = password;

        // console.log(form);
        if (password != null) {
          // Loading msg
          message.loading({ content: "Action in progress...", key });

          // Calling the API
          ServerService.DeleteFields(form)
            .then((res) => {
              // console.log(res);
              message.success({
                content: "Deletion successful!",
                key,
                duration: 2,
              });
              // creating a deep copy of data
              let Data_Copy = { ...data };
              // using parseInt as the index is string
              Data_Copy.sessions.splice(parseInt(sessionData[1]), 1);
              setData(Data_Copy);
              setSession(null);
            })
            .catch((err) => {
              // console.log(err);
              message.error({
                content: "Something went wrong!",
                key,
                duration: 2,
              });
            });
        }
      }
    } 
    else if (option === "exam") {
      if (examType == null) {
        message.error({ content: "Exam-type not selected!", key, duration: 2 });
      } 
      else {
        // using split to seperate id and index of exam
        const ExamData = examType[0].split(",");

        form["id"] = ExamData[0];

        const password = prompt("Enter Password");
        form["password"] = password;

        // console.log(form);
        if (password != null) {
          // Loading msg
          message.loading({ content: "Action in progress...", key });

          // Calling the API
          ServerService.DeleteFields(form)
            .then((res) => {
              // console.log(res);
              message.success({
                content: "Deletion successful!",
                key,
                duration: 2,
              });

              // Creating deep copy of data
              let Data_Copy = { ...data };
              // using parseInt as the index is string
              Data_Copy.exams.splice(parseInt(ExamData[1]), 1);
              setData(Data_Copy);
              setExamType(null);
            })
            .catch((err) => {
              // console.log(err);
              message.error({
                content: "Something went wrong!",
                key,
                duration: 2,
              });
            });
        }
      }
    }
  };

  // To fetch data when the page loads
  React.useEffect(() => {
    // calling the API
    ServerService.getFields()
      .then((res) => {
        // console.log(res);
        setData(res.data);
      })
      .catch((err) => {
        // console.log(err);
      });
  }, []);

  return (
    <div>
      <AdminNavbar />
      <div className={styles.Delete}>
        <div className={styles.DeleteArea}>
          <div className={styles.DeleteDropdowns}>
            <label htmlFor="Session">Session</label>
            <select
              name="Session"
              id="Session"
              defaultValue="DEFAULT"
              onClick={(e) => saveHandler(e, "session")}
            >
              <option disabled value="DEFAULT">
                No selected session
              </option>

              {data != null
                ? data.sessions.map((session, index) => {
                    return (
                      <option key={index} value={[session.sessionId, index]}>
                        {session.session}
                      </option>
                    );
                  })
                : null}
            </select>

            <Button
              onClick={() => DeleteHandler("session")}
              type="primary"
              danger
            >
              Delete Session
            </Button>
          </div>

          <div className={styles.DeleteDropdowns}>
            <label htmlFor="ExamTypes">Exam Types</label>
            <select
              required
              name="ExamTypes"
              id="ExamTypes"
              defaultValue="DEFAULT"
              onClick={(e) => saveHandler(e, "exam")}
            >
              <option disabled value="DEFAULT">
                No selected Exam
              </option>
              {data != null
                ? data.exams.map((type, index) => {
                    // console.log(index)
                    return (
                      <option key={index} value={[type.examId, index]}>
                        {type.exam}
                      </option>
                    );
                  })
                : null}
            </select>
            <Button onClick={() => DeleteHandler("exam")} type="primary" danger>
              Delete Exam Type
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}
