import React, { useState } from "react";
import "./Login.css";
import { Redirect } from "react-router-dom";
import AdminNavbar from "../../../Components/Admin/Navbar/Navbar";
import { Form, Input, Button } from "antd";
import ServerService from "../../../API/ServerService";
import { message } from "antd";
//import { setAdmin } from "../../../store/actions/actions";

function Login() {
  const [redirect, setRedirect] = useState(null);

  const forgotPassword = () => {
    //loading msg
    let key = "updatable";
    message.loading({ content: "Action in progress...", key });

    // Calling the Forgot password API
    ServerService.forgotPassword()
      .then((res) => {
        message.success({
          content: "E-Mail sent successfully!",
          key,
          duration: 2,
        });
      })
      .catch((err) => {
        // console.log(err);
        message.error({ content: "Something went wrong!", key, duration: 2 });
      });
  };

  const layout = {
    labelCol: {
      span: 4,
    },
    wrapperCol: {
      span: 4,
    },
  };

  const tailLayout = {
    wrapperCol: {
      offset: 4,
      span: 4,
    },
  };

  const onFinish = (values) => {
    // console.log("Values:", values);

    //loading msg
    let key = "updatable";
    message.loading({ content: "Action in progress...", key });

    // Calling the login API
    ServerService.Login(values)
      .then((res) => {
        // console.log(res);
        message.success({
          content: "Logged in successfully!",
          key,
          duration: 2,
        });
        localStorage.setItem("token", res.data.token);
        setRedirect("/siadmin/AddPapers");
      })
      .catch((err) => {
        message.error({
          content: "Password entered is incorrect!",
          key,
          duration: 2,
        });
        // console.log(err);
      });
  };

  // If validation fails
  const onFinishFailed = (errorInfo) => {
    // console.log("Failed:", errorInfo);
  };

  // to redirect to addView Route
  if (redirect != null) {
    return <Redirect to={redirect} />;
  }

  return (
    <div>
      <AdminNavbar />
      <Form
        {...layout}
        name="basic"
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item
          label="Username"
          name="username"
          rules={[
            {
              required: true,
              message: "Please input your username!",
            },
          ]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="Password"
          name="password"
          rules={[
            {
              required: true,
              message: "Please input your password!",
            },
          ]}
        >
          <Input.Password />
        </Form.Item>
        <p
          className="forgotPass"
          onClick={forgotPassword}
        >
          Forgot Password?
        </p>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Login
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}

export default Login;
