import React, { useState, useEffect } from "react";
import AdminNavbar from "../../../Components/Admin/Navbar/Navbar";
import styles from "./AddPapers.module.css";
import { message, Spin } from "antd";
import { ExclamationCircleTwoTone } from "@ant-design/icons";
import ServerService from "../../../API/ServerService";
import DropZone from "../../../Components/FileDropZone/DropZone";
import { Modal } from "react-bootstrap";

export default function AddPapers() {
  // variables initialisation
  const [data, setData] = useState(false);
  const [selectedData, setSelectedData] = useState({
    branch: null,
    course: null,
    examType: null,
    paperType: null,
    semesterType: null,
    session: null,
  });
  const [course, setCourse] = useState(null);
  const [filesToUpload, setFilesToUpload] = useState([]);

  // for handling modal
  const [show, setShow] = useState(false);
  const [docsList, setDocsList] = useState([]);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // methods
  const courseHandler = (e) => {
    setCourse(e.target.value);
    setSelectedData((prevState) => ({
      ...prevState,
      branch: "DEFAULT",
    }));
  };

  const updateFiles = (files) => {
    setFilesToUpload(files);
    // console.log(files);
  };

  // calling uploading api
  const Upload = (event) => {
    event.preventDefault();
    event.stopPropagation();

    // using spread operator to include the selectedData object to form
    const form = { ...selectedData };
    form["course"] = course;

    // console.log(form);

    // creating formData to upload docs and other details
    let uploadData = new FormData();
    uploadData.append("branch", form["branch"]);
    uploadData.append("course", form["course"]);
    uploadData.append("examType", form["examType"]);
    uploadData.append("paperType", form["paperType"]);
    uploadData.append("semesterType", form["semesterType"]);
    uploadData.append("session", form["session"]);

    // To add doc file in the list of upload Data [{},{}..]
    filesToUpload.forEach((file) => {
      uploadData.append("files", file);
    });

    // validation check
    if (filesToUpload.length !== 0 && form["branch"] != null) {
      //loading msg
      let key = "updatable";
      message.loading({ content: "Action in progress...", key });

      // API CALLING
      ServerService.UploadData(uploadData)
        .then((res) => {
          if (res.status === 200) {
            if (res.data.remainingFiles.length === 0) {
              message.success({
                content: "Documents uploaded successfully!",
                key,
                duration: 2,
              });
            } else {
              setDocsList(res.data.remainingFiles);
              handleShow();
            }
          }
        })
        .catch((err) => {
          // console.log(err);
          message.error({ content: "Something went wrong!", key, duration: 2 });
        });
    } else {
      // validation check for files
      let key = "updatable";
      if (filesToUpload.length === 0) {
        message.error({ content: "Upload files first!", key, duration: 2 });
      } else {
        message.error({ content: "Select branch first!", key, duration: 2 });
      }
    }
  };

  // Saving the selected options in state
  const saveHandler = (e, name) => {
    // console.log(name);
    setSelectedData((prevState) => ({
      ...prevState,
      [name]: e.target.value,
    }));
  };

  // Fetches details when the page loads
  useEffect(() => {
    ServerService.getUploadData()
      .then((res) => {
        // console.log(res);
        setData(res.data);
      })
      .catch((err) => {
        // console.log(err);
      });
  }, []);

  return (
    <>
      <div>
        <AdminNavbar />
        <div className={styles.AddPapers}>
          {!data ? (
            <div className="loading">
              <Spin size="large" />
              <span style={{ marginTop: "8px" }}>
                Your Data will be available shortly.
              </span>
            </div>
          ) : (
            <>
              <form onSubmit={(event) => Upload(event)}>
                <div className={styles.AddPapers_Container}>
                  <label htmlFor="ExamTypes">Exam Types</label>
                  <select
                    required
                    name="ExamTypes"
                    id="ExamTypes"
                    defaultValue="DEFAULT"
                    onChange={(e) => saveHandler(e, "examType")}
                  >
                    <option disabled value="DEFAULT">
                      No Selected Examtype
                    </option>
                    {data.examTypes.map((type, index) => {
                      return (
                        <option key={index} value={type}>
                          {type}
                        </option>
                      );
                    })}
                  </select>
                </div>

                <div className={styles.AddPapers_Container}>
                  <label htmlFor="Sessions">Sessions</label>
                  <select
                    required
                    name="Sessions"
                    id="Sessions"
                    defaultValue="DEFAULT"
                    onChange={(e) => saveHandler(e, "session")}
                  >
                    <option disabled value="DEFAULT">
                      No Selected Session
                    </option>
                    {data.sessions.map((Session, index) => {
                      return (
                        <option key={index} value={Session}>
                          {Session}
                        </option>
                      );
                    })}
                  </select>
                </div>

                <div className={styles.AddPapers_Container}>
                  <label htmlFor="Course">Course</label>
                  <select
                    required
                    name="Course"
                    id="Course"
                    defaultValue="DEFAULT"
                    onChange={(event) => courseHandler(event)}
                  >
                    <option disabled value="DEFAULT">
                      No Selected Course
                    </option>
                    {data.courseBranch.map((course, index) => {
                      return (
                        <option key={index} value={course.course}>
                          {course.course}
                        </option>
                      );
                    })}
                  </select>
                </div>

                <div className={styles.AddPapers_Container}>
                  <label htmlFor="Branch">Branch</label>
                  <select
                    name="Branch"
                    id="Branch"
                    defaultValue="DEFAULT"
                    value={selectedData.branch}
                    onChange={(e) => saveHandler(e, "branch")}
                  >
                    {course != null ? (
                      <>
                        <option disabled value="DEFAULT">
                          No Selected Branches
                        </option>
                        {data.courseBranch
                          .filter((Course) => {
                            return Course.course === course;
                          })[0]
                          .branches.map((branch, index) => {
                            return (
                              <React.Fragment key={index}>
                                <option value={branch}>{branch}</option>
                              </React.Fragment>
                            );
                          })}
                      </>
                    ) : (
                      <option>Select a Course first</option>
                    )}
                  </select>
                </div>

                <div className={styles.AddPapers_Container}>
                  <label htmlFor="sem">Semester</label>
                  <select
                    name="sem"
                    id="sem"
                    defaultValue="DEFAULT"
                    onChange={(e) => saveHandler(e, "semesterType")}
                  >
                    <option disabled value="DEFAULT">
                      No Selected Semester Type
                    </option>
                    <option value={"ODD"}>Odd</option>
                    <option value={"EVEN"}>Even</option>
                  </select>
                </div>

                <div className={styles.AddPapers_Container}>
                  <label htmlFor="paper">Paper Types</label>
                  <select
                    name="paper"
                    id="paper"
                    defaultValue="DEFAULT"
                    onChange={(e) => saveHandler(e, "paperType")}
                  >
                    <option disabled value="DEFAULT">
                      No Selected Paper type
                    </option>
                    <option value={"QUES"}>Question papers</option>
                    <option value={"SOL"}>Answer papers</option>
                  </select>
                </div>

                <DropZone updateFiles={updateFiles} />

                <div className={styles.sumbit}>
                  <button type="submit">Upload</button>
                </div>
              </form>
            </>
          )}
        </div>
      </div>
      <Modal show={show} onHide={handleClose} centered>
        <div className={styles.docsList}>
          <h5>
            Upload Successful
            <ExclamationCircleTwoTone
              twoToneColor="red"
              style={{
                marginLeft: "8px",
              }}
            />
          </h5>
          <i class="fas fa-times" onClick={handleClose} />
          <hr />
          <h6 className={styles.subHead}>
            These document(s){" "}
            <span style={{ textDecoration: "underline" }}>
              were not uploaded
            </span>
            {", "}
            as they were already uploaded with the same fields.
          </h6>
          <div className={styles.listDisplay}>
            {docsList.map((docName, index) => {
              return (
                <p className={styles.papers} key={index}>
                  {docName}
                </p>
              );
            })}
          </div>
        </div>
      </Modal>
    </>
  );
}
