import React from "react";
import { useParams, useHistory } from "react-router-dom";
import { Form, Input, Button } from "antd";
import AdminNavbar from "../../../Components/Admin/Navbar/Navbar";
import { message } from "antd";
import ServerService from "../../../API/ServerService";

const ResetPassword = () => {
  let { token } = useParams();
  let history = useHistory();

  const layout = {
    labelCol: {
      span: 4,
    },
    wrapperCol: {
      span: 4,
    },
  };

  const tailLayout = {
    wrapperCol: {
      offset: 4,
      span: 4,
    },
  };

  const onFinish = (values) => {

    let newPassData = {
      newPassword: values.newPassword,
    };

    //loading msg
    let key = "updatable";
    message.loading({ content: "Action in progress...", key });

    ServerService.resetPassword(token, newPassData)
      .then((res) => {
        if (res.status === 200) {
          message.success({
            content: "Password changed successfully!",
            key,
            duration: 2,
          });
          setTimeout(() => {
            history.push("/admin/Login");
          }, 2000);
        }
      })
      .catch((err) => {
        if (err.response.status === 417) {
          message.error({
            content: "Reset-link expired, resend Email! ",
            key,
            duration: 2,
          });
        }
      });
  };

  const onFinishFailed = (errorInfo) => {
    // console.log("Failed:", errorInfo);
  };

  const resendMail = () => {
    //loading msg
    let key = "updatable";
    message.loading({ content: "Action in progress..." });

    ServerService.forgotPassword()
      .then((res) => {
        // console.log(res);
        message.success({
          content: "E-Mail sent successfully!",
          key,
          duration: 2,
        });
      })
      .catch((err) => {
        // console.log(err);
        message.error({ content: "Something went wrong!", key, duration: 2 });
      });
  };

  return (
    <div>
      <AdminNavbar />
      <Form
        {...layout}
        name="basic"
        initialValues={{ remember: true }}
        onFinish={onFinish}
        onFinishFailed={onFinishFailed}
      >
        <Form.Item
          label="New Password"
          name="newPassword"
          rules={[
            {
              required: true,
              message: "Please input your password!",
            },
          ]}
        >
          <Input.Password />
        </Form.Item>

        <Form.Item
          label="Confirm New Password"
          name="confirm-new-password"
          rules={[
            {
              required: true,
              message: "Please input your password!",
            },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || getFieldValue("newPassword") === value) {
                  return Promise.resolve();
                }
                return Promise.reject(
                  new Error("The two passwords that you entered do not match!")
                );
              },
            }),
          ]}
        >
          <Input.Password />
        </Form.Item>

        <p
          style={{ marginLeft: "200px", cursor: "pointer" }}
          onClick={resendMail}
        >
          Resend E-Mail
        </p>

        <Form.Item {...tailLayout}>
          <Button type="primary" htmlType="submit">
            Reset Password
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default ResetPassword;
