import React, { useEffect, useState } from "react";
import AdminNavbar from "../../../Components/Admin/Navbar/Navbar";
import styles from "./ViewPapers.module.css";
import ServerService from "../../../API/ServerService";
import SessionsTabsDisplay from "./SessionsTabsDisplay";
import { message } from "antd";
import { DeleteTwoTone } from "@ant-design/icons";
import { Collapse } from "antd";
import { Tabs,Spin } from "antd";

const { Panel } = Collapse;
const { TabPane } = Tabs;

const ViewPapers = () => {
  // Initilisation of state
  const [allData, setAllData] = useState(null);

  useEffect(() => {
    ServerService.fetchAllPapers()
      .then((res) => {
        // console.log(res);
        setAllData(res.data);
      })
      .catch((err) => {
        // console.log(err);
      });
  }, []);

  // checking if onClick runs
  const courseHandler = (key) => {
    // console.log(key);
  };

  const branchHandler = (key) => {
    // console.log(key);
  };

  // function to delete the entire branch
  const deleteBranch = (e, course, branch, courseIndex, branchIndex) => {
    e.preventDefault();
    const password = prompt("Please enter your password");
    // console.log(course, branch, courseIndex, branchIndex);

    let deletePaperData = {
      courseId: course.courseId,
      branchId: branch.branchId,
      sessionId: "",
      examId: "",
      paperType: "",
      documentId: "",
      password: password,
    };
    // console.log(deletePaperData);

    if (password !== null) {
      //loading msg
      let key = "updatable";
      message.loading({ content: "Action in progress...", key });

      // Calling the API
      ServerService.DeleteTypes(deletePaperData)
        .then((res) => {
          // console.log(res);
          if (res.status === 200) {
            let newCourse = [...allData];
            // console.log(newCourse);
            newCourse[courseIndex].branches.splice(branchIndex, 1);
            // console.log(newCourse);
            setAllData(newCourse);
            message.success({
              content: "Branch deleted!",
              key,
              duration: 2,
            });
          }
        })
        .catch((err) => {
          // console.log(err);
          message.error({ content: "Something went wrong!", key, duration: 2 });
        });
    }
  };

  // function to delete the entire Course
  const deleteCourse = (e, course, courseIndex) => {
    e.preventDefault();
    const password = prompt("Please enter your password");
    // console.log(course, courseIndex);

    let deletePaperData = {
      courseId: course.courseId,
      branchId: "",
      sessionId: "",
      examId: "",
      paperType: "",
      documentId: "",
      password: password,
    };
    // console.log(deletePaperData);

    if (password !== null) {
      //loading msg
      let key = "updatable";
      message.loading({ content: "Action in progress...", key });

      // Calling the API
      ServerService.DeleteTypes(deletePaperData)
        .then((res) => {
          // console.log(res);
          if (res.status === 200) {
            let newCourse = [...allData];
            // console.log(newCourse);
            newCourse.splice(courseIndex, 1);
            // console.log(newCourse);
            setAllData(newCourse);
            message.success({
              content: "Course deleted!",
              key,
              duration: 2,
            });
          }
        })
        .catch((err) => {
          // console.log(err);
          message.error({ content: "Something went wrong!", key, duration: 2 });
        });
    }
  };

  return (
    <div>
      <AdminNavbar />
      <div className={styles.papersDisplay}>
        {allData ? (
          <Collapse onChange={courseHandler}>
          {allData
            ? allData.map((course, courseIndex) => {
                let branches = course.branches;

                return (
                  <Panel
                    key={courseIndex}
                    header={
                      <>
                        <span>{course.course} &nbsp;</span>
                        <span
                          style={{ color: "grey" }}
                        >{`(${branches.length} branches)`}</span>
                        <h6
                          className="deleteCourseIcon"
                          onClick={(e) => deleteCourse(e, course, courseIndex)}
                        >
                          Delete Course
                          <DeleteTwoTone
                            style={{
                              transform: "scale(1.3) translateY(-1.8px)",
                              cursor: "pointer",
                              marginLeft: "14px",
                            }}
                            twoToneColor="red"
                          />
                        </h6>
                      </>
                    }
                  >
                    <Tabs
                      defaultActiveKey="0"
                      tabPosition={"top"}
                      // style={{ height: 220 }}
                      onChange={branchHandler}
                    >
                      {branches.map((branch, branchIndex) => {
                        return (
                          <TabPane
                            tab={
                              <>
                                <span>{branch.branch}</span>{" "}
                                <span
                                  style={{ color: "grey" }}
                                >{`(${branch.sessions.length} sessions)`}</span>
                              </>
                            }
                            key={branchIndex}
                          >
                            <h6
                              className="deleteBranchIcon"
                              onClick={(e) =>
                                deleteBranch(
                                  e,
                                  course,
                                  branch,
                                  courseIndex,
                                  branchIndex
                                )
                              }
                            >
                              Delete Branch data
                              <DeleteTwoTone
                                style={{
                                  transform: "scale(1.3) translateY(-1.8px)",
                                  cursor: "pointer",
                                  marginLeft: "14px",
                                }}
                                twoToneColor="red"
                              />
                            </h6>
                            <SessionsTabsDisplay
                              branchData={branch}
                              courseData={course}
                              sessionsData={branch.sessions}
                            />
                          </TabPane>
                        );
                      })}
                    </Tabs>
                  </Panel>
                );
              })
            : null}
        </Collapse>
        ) : 
        // <span >
        //   Please wait till the papers are being fetched. LOADING ...</span>
            <div className="loading"><Spin size="large"/>
              <span style={{marginTop:"8px"}}>
                <b style={{fontWeight:'400'}}>Please wait </b>till the papers are being fetched from the server.
              </span>
            </div>
          }
        
      </div>
    </div>
  );
};

export default ViewPapers;
